.tom-hairtsag{
border: 1px solid black;
height: 70vh;
width:60vw;
cursor: pointer;
margin-top: 20px ;
margin-inline: 20px;
border-radius: 30px;
display: flex;
justify-content: center;
align-items: center;
font-size: 60px ;
transition: 0.1s;
}

.tom-hairtsag:active{
    /* background-color: gold ; */
    transform: scale(5px) ;
}


.hairtsag{

    background-color: blueviolet;
    width: 19vh;
    height: 200px;
    border-radius: 30px;
    color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
margin-right: 20px  ;
}
.pink{
    background-color: pink;
}
.orange{
    background-color: orange;
}
.green{
    background-color: green;
}
.hairtsag h1{
    font-size: 60px;
    font-weight: bold;
    margin: 0;
}
.hairtsag p{
    font-size: 32px;
}
.aaa{
     display: flex;
    justify-content: center;

}
.main{
    display: flex;
    flex-direction: column;
    align-items: center;
}