.container{
    background-color: #eaf0f1;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction:column ;
}
.tic-tac{
    width: 300px;
    height: 300px;
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 10px;

}
.box{
    background-color: #8b78e6 ;
    width: 100px;
    height: 100px;
    border-radius: 16px;
    font-size: 50px;
    display: flex;
    justify-content: center;
}
.box:hover{
    background-color: #f3b431 ;
    cursor: pointer;
}
.container  h1{
    font-size: 50px;
    color: #ff006e ;
}